@import url(~prettify/src/prettify.css);
pre.prettyprint {
  padding: 1.4rem 2rem;
  font-weight: 700;
  border: none;
  background-color: #353535;
}
pre.prettyprint code {
    background-color: inherit;
}
pre.prettyprint .tag,
  pre.prettyprint .atn,
  pre.prettyprint .pun {
    color: #6cd7f7;
}
pre.prettyprint .atv {
    color: #bbe888;
}
pre.prettyprint .pln {
    color: #fff;
}
pre.prettyprint .com {
    color: #a9a9a9;
    font-style: italic;
}
pre.prettyprint.language-css .str {
    color: #bbe888;
}
pre.prettyprint.language-css .pun,
  pre.prettyprint.language-css .pun + .pln,
  pre.prettyprint.language-css .kwd {
    color: #6cd7f7;
}
pre.prettyprint.language-css .pun + .pln {
    font-weight: 700;
}
pre.prettyprint.language-css .lit {
    color: #f36c44;
}



